// app/page.tsx
"use client";

import React, { useEffect, useRef, useState } from "react";
import { v4 as uuidv4 } from "uuid";
import TransactionDrawer from "@/components/TransactionDrawer";
import Panel from "@/components/Panel";

type Agent = { id: string; name: string; url: string; side: "left" | "right" };

const AGENTS: Agent[] = [
  { id: "agent-A", name: "Agent A", url: "http://localhost:3001", side: "left" },
  { id: "agent-B", name: "Agent B", url: "http://localhost:3002", side: "right" },
];

function nowIso() {
  return new Date().toISOString();
}

type ChatMsg = {
  id: string;
  fromUrl: string;
  toUrl: string;
  text: string;
  ts: string;
  status: "sending" | "sent" | "waiting" | "received" | "error";
  meta?: any;
};

export default function IndexPage() {
  const [fromAgentUrl, setFromAgentUrl] = useState<string>(AGENTS[0].url);
  const [toAgentUrl, setToAgentUrl] = useState<string>(AGENTS[1].url);
  const [text, setText] = useState("");
  const [messages, setMessages] = useState<ChatMsg[]>([]);
  const [sending, setSending] = useState(false);
  const [drawerTx, setDrawerTx] = useState<any | null>(null);
  const [panelIntent, setPanelIntent] = useState<any | null>(null);

  const scrollRef = useRef<HTMLDivElement | null>(null);
  useEffect(() => {
    if (scrollRef.current) scrollRef.current.scrollTop = scrollRef.current.scrollHeight;
  }, [messages]);

  function pushMessage(m: ChatMsg) {
    setMessages((s) => [...s, m]);
  }

  function agentName(url: string) {
    const a = AGENTS.find((x) => x.url === url);
    return a ? a.name : url;
  }

  // small loading dots
  function LoadingDots() {
    const [dots, setDots] = useState("");
    useEffect(() => {
      const iv = setInterval(() => setDots((d) => (d.length < 3 ? d + "." : "")), 400);
      return () => clearInterval(iv);
    }, []);
    return <span className="text-sm text-gray-500 italic">{dots}</span>;
  }

  // simplified normalizer from your page (kept minimal)
  function normalizeResponsePayload(raw: any): { display: string; raw: any } {
    if (raw === null || raw === undefined) return { display: "", raw };
    if (typeof raw === "string") {
      try {
        const parsed = JSON.parse(raw);
        return normalizeResponsePayload(parsed);
      } catch {
        return { display: raw, raw };
      }
    }
    if (typeof raw === "object") {
      if (raw.responsePayload) return normalizeResponsePayload(raw.responsePayload);
      if (raw.result) return { display: String(raw.result), raw };
      if (raw.text) return { display: String(raw.text), raw };
      try {
        return { display: JSON.stringify(raw, null, 2), raw };
      } catch {
        return { display: String(raw), raw };
      }
    }
    return { display: String(raw), raw };
  }

  // minimal poller (same idea you had)
  async function pollForResponse(senderUrl: string, intentId: string, maxMs = 20000, intervalMs = 800) {
    const start = Date.now();
    while (Date.now() - start < maxMs) {
      try {
        const res = await fetch(`${senderUrl.replace(/\/$/, "")}/status/${intentId}`);
        if (res.status === 404) {
          // not ready yet
        } else {
          const json = await res.json();
          let candidate: any = json.response || json.response_payload || json.responsePayload || json;
          if (candidate && (candidate.result || candidate.text || Object.keys(candidate).length > 0)) {
            return candidate;
          }
        }
      } catch {
        // ignore transient
      }
      await new Promise((r) => setTimeout(r, intervalMs));
    }
    throw new Error("poll timeout");
  }

  // handle send (uses send-request endpoint)
  async function handleSend() {
    if (!text.trim()) return;
    setSending(true);
    const localId = uuidv4();
    const outgoing: ChatMsg = {
      id: localId,
      fromUrl: fromAgentUrl,
      toUrl: toAgentUrl,
      text,
      ts: nowIso(),
      status: "sending",
    };
    pushMessage(outgoing);
    const payload = { receiverUrl: toAgentUrl, payload: { text } };
    setText("");

    try {
      const sendUrl = `${fromAgentUrl.replace(/\/$/, "")}/send-request`;
      const res = await fetch(sendUrl, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload),
      });

      const txt = await res.text();
      let parsed: any;
      try {
        parsed = JSON.parse(txt);
      } catch {
        parsed = txt;
      }

      setMessages((m) =>
        m.map((msg) =>
          msg.id === localId ? { ...msg, status: "sent", meta: { proxied_status: res.status, proxied_body: parsed } } : msg
        )
      );

      const intentId: string | null = parsed && typeof parsed === "object" && parsed.intent_id ? parsed.intent_id : null;

      if (intentId) {
        setMessages((m) => m.map((msg) => (msg.id === localId ? { ...msg, status: "waiting" } : msg)));
        try {
          const rawPayload = await pollForResponse(fromAgentUrl, intentId, 30000, 700);
          const normalized = normalizeResponsePayload(rawPayload);
          const incomingText = normalized.display;

          const incoming: ChatMsg = {
            id: uuidv4(),
            fromUrl: toAgentUrl,
            toUrl: fromAgentUrl,
            text: incomingText,
            ts: nowIso(),
            status: "received",
            meta: { intentId, raw: normalized.raw },
          };
          pushMessage(incoming);

          setMessages((m) => m.map((msg) => (msg.id === localId ? { ...msg, status: "sent" } : msg)));
        } catch (err: any) {
          pushMessage({
            id: uuidv4(),
            fromUrl: "system",
            toUrl: fromAgentUrl,
            text: `No response available yet for intent ${intentId} (timeout).`,
            ts: nowIso(),
            status: "error",
            meta: { intentId },
          });

          setMessages((m) => m.map((msg) => (msg.id === localId ? { ...msg, status: "error" } : msg)));
        }
      } else {
        const normalized = normalizeResponsePayload(parsed);
        pushMessage({
          id: uuidv4(),
          fromUrl: toAgentUrl,
          toUrl: fromAgentUrl,
          text: normalized.display,
          ts: nowIso(),
          status: "received",
          meta: { raw: normalized.raw },
        });
      }
    } catch (err: any) {
      setMessages((m) => m.map((msg) => (msg.id === localId ? { ...msg, status: "error", meta: { error: String(err) } } : msg)));
      pushMessage({
        id: uuidv4(),
        fromUrl: "system",
        toUrl: fromAgentUrl,
        text: `Error sending: ${String(err.message || err)}`,
        ts: nowIso(),
        status: "error",
      });
    } finally {
      setSending(false);
    }
  }

  // for demo: open tx in drawer (mock tx object)
  function openTxDrawerFromMessage(m: ChatMsg) {
    // if meta has intentId or tx info, show it; otherwise show minimal
    const tx = m.meta?.tx || (m.meta?.intentId ? { intentId: m.meta.intentId, status: m.status } : null);
    setDrawerTx(tx ?? { note: "No on-chain data for this message", message: m.text, meta: m.meta });
  }

  return (
    <div className="h-screen flex flex-col p-6 bg-gray-50 dark:bg-slate-900">
      <header className="flex items-center justify-between mb-4">
        <h1 className="text-lg font-bold">Agent Chat — MVP</h1>
        <div className="text-sm text-gray-600 dark:text-gray-300">
          {AGENTS.find((a) => a.url === fromAgentUrl)?.name} → {AGENTS.find((a) => a.url === toAgentUrl)?.name}
        </div>
      </header>

      <div className="flex flex-1 gap-6">
        {/* Main chat area */}
        <section className="flex-1 bg-white dark:bg-slate-800 rounded-md shadow-sm p-4 flex flex-col">
          <div ref={scrollRef} className="flex-1 overflow-auto pb-4">
            {messages.length === 0 && <div className="text-center text-gray-500 mt-12">No messages yet — send one.</div>}
            {messages.map((m) => {
              if (m.fromUrl === "system") {
                return (
                  <div key={m.id} className="text-center text-gray-500 my-3">
                    <small>{m.text}</small>
                  </div>
                );
              }
              const alignLeft = AGENTS.find((a) => a.url === m.fromUrl)?.side === "left";
              return (
                <div key={m.id} className={`flex ${alignLeft ? "justify-start" : "justify-end"} mb-3`}>
                  <div className="max-w-[72%] p-3 rounded-lg bg-slate-900 text-white font-mono text-sm">
                    <div className="font-semibold mb-2">{agentName(m.fromUrl)}</div>
                    <div className="whitespace-pre-wrap">{m.text}</div>

                    {m.status === "waiting" && (
                      <div className="mt-2 text-xs text-gray-400 flex items-center gap-2">
                        <span>Waiting for response</span>
                        <LoadingDots />
                      </div>
                    )}

                    <div className="mt-2 text-xs text-gray-400 flex justify-between">
                      <span>{new Date(m.ts).toLocaleTimeString()}</span>
                      <div>
                        <em>{m.status}</em>
                        <button
                          onClick={() => openTxDrawerFromMessage(m)}
                          className="ml-3 text-xs px-2 py-1 rounded bg-gray-200 dark:bg-slate-700 text-gray-800 dark:text-gray-200"
                        >
                          Inspect
                        </button>
                        {/* open panel to review intent if meta.intentId */}
                        {m.meta?.intentId && (
                          <button
                            onClick={() => setPanelIntent({ intentId: m.meta.intentId, from: m.fromUrl, payload: m.meta.raw })}
                            className="ml-2 text-xs px-2 py-1 rounded bg-blue-500 text-white"
                          >
                            Review Intent
                          </button>
                        )}
                      </div>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>

          <div className="mt-3 flex items-end gap-3">
            <div className="flex flex-col">
              <label className="text-xs text-gray-600">From</label>
              <select value={fromAgentUrl} onChange={(e) => setFromAgentUrl(e.target.value)} className="p-2 rounded">
                {AGENTS.map((a) => (
                  <option key={a.id} value={a.url}>
                    {a.name}
                  </option>
                ))}
              </select>
            </div>

            <div className="flex flex-col">
              <label className="text-xs text-gray-600">To</label>
              <select value={toAgentUrl} onChange={(e) => setToAgentUrl(e.target.value)} className="p-2 rounded">
                {AGENTS.map((a) => (
                  <option key={a.id} value={a.url}>
                    {a.name}
                  </option>
                ))}
              </select>
            </div>

            <div className="flex-1">
              <label className="text-xs text-gray-600">Message</label>
              <textarea
                value={text}
                onChange={(e) => setText(e.target.value)}
                rows={2}
                placeholder='Type "Hello" and press Enter'
                className="w-full rounded p-2 font-mono"
                onKeyDown={(e) => {
                  if (e.key === "Enter" && !e.shiftKey) {
                    e.preventDefault();
                    void handleSend();
                  }
                }}
              />
            </div>

            <div className="flex flex-col">
              <button onClick={() => void handleSend()} disabled={sending} className="px-4 py-2 rounded bg-slate-900 text-white">
                {sending ? "Sending…" : "Send"}
              </button>
              <button
                onClick={() => setMessages([])}
                className="mt-2 px-2 py-1 rounded border text-sm text-gray-700 dark:text-gray-300"
              >
                Clear
              </button>
            </div>
          </div>
        </section>

        {/* Sidebar */}
        <aside className="w-80 flex flex-col gap-4">
          <div className="p-3 bg-white dark:bg-slate-800 rounded shadow-sm">
            <div className="font-semibold mb-2">Agents</div>
            {AGENTS.map((a) => (
              <div key={a.id} className="flex items-center justify-between py-2 border-b last:border-b-0">
                <div>
                  <div className="font-medium">{a.name}</div>
                  <div className="text-xs text-gray-500">{a.url}</div>
                </div>
                <div>
                  <button
                    onClick={() => navigator.clipboard?.writeText(a.url)}
                    className="px-2 py-1 rounded bg-gray-200 dark:bg-slate-700 text-xs"
                  >
                    Copy
                  </button>
                </div>
              </div>
            ))}
          </div>

          <div className="p-3 bg-white dark:bg-slate-800 rounded shadow-sm flex-1 overflow-auto">
            <div className="font-semibold mb-2">Recent activity</div>
            {messages.slice().reverse().slice(0, 8).map((m) => (
              <div key={m.id} className="mb-3">
                <div className="text-xs text-gray-500">
                  {new Date(m.ts).toLocaleTimeString()} — <strong>{m.fromUrl === "system" ? "System" : agentName(m.fromUrl)}</strong>
                </div>
                <div className="truncate text-sm">{m.text}</div>
              </div>
            ))}
            {messages.length === 0 && <div className="text-gray-400">No activity yet.</div>}
          </div>
        </aside>
      </div>

      {/* Transaction Drawer */}
      <TransactionDrawer tx={drawerTx} onClose={() => setDrawerTx(null)} />

      {/* Intent Review Panel (modal/panel) */}
      {panelIntent && <Panel intent={panelIntent} onClose={() => setPanelIntent(null)} />}
    </div>
  );
}
